.presentation {
    display: flex;
}

.photo {
    border-radius: 50%;
    border-style: solid;
    width: 10em;
    height: 10em;
    min-width: 10em;
    min-height: 10em;
    background-image: url('../../data/photo.jpg'), linear-gradient(to bottom, rgba(48, 48, 47), rgba(0, 0, 255));
    background-size: cover;
    display: inline-block;
    margin-left: 1em;
    margin-top: 1em;
}

.muiDivideRoot.customDivide {
    border-top-width: 0.1em;
    border-bottom-width: 0;
    text-align: left;
    max-width: 5.8em;
    margin-top: 0.2em;
    margin-bottom: 0.1em;
}

.texte {
    text-align: left;
    margin-top: 0.5em;
    margin-left: 2em;
    font-size: 1.9em;
}

.poste {
    font-style: italic;
    font-size: 0.6em;
    opacity: 0.6;
}

.description {
    margin-top: 1em;
    font-size: 0.65em;
}