.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;

  background-image: linear-gradient(to bottom, rgba(48, 48, 47, 0.5), rgba(0, 0, 255, 0.5)), url('../src/data/stars.jpg');
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.mini-logo {
  height: 2.5rem;
  pointer-events: none;
}

.mini-logo-span {
  margin-left: auto;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-float infinite 3s ease-in-out;
  }

  .mini-logo {
    animation: App-logo-float infinite 3s ease-in-out;
  }
}

.App-header {
  display: flex;
  font-size: inherit;
}

.App-link {
  color: rgb(112, 76, 182);
}

@keyframes App-logo-float {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(0px);
  }
}
