.datelieu {
    padding-top: 1em;
    max-width: 8em;
    font-size: 0.8em;
    font-weight: bold;
    display: flex;
    flex-direction: column;
    gap: 1em;
    opacity: 0.6;
}

.date {
    display: flex;
    align-items: flex-start;
}

.lieu {
    display: flex;
    align-items: flex-start;
    text-transform: uppercase;
}