.contact {
    text-transform: uppercase;
    font-weight: bold;
    margin-top: 0.3em;
    display: flex;
    justify-content: center;
}

.infos {
    align-self: flex-start;
}

.info {
    display: flex;
    align-items: center;
    font-size: 1em;
    gap: 0.5em;
    margin-left: 0.5em;
}

.info+.info {
    margin-bottom: 0.5em;
    margin-top: 0.5em;
}

.muiDivideRoot.customDivide {
    border-width: 0.05em;
    margin-top: 0.3em;
    margin-bottom: 0.3em;
}